body {
    margin: 0;
    background-color: #62bbe7;
    font-size: 5vh;
    /* padding: 2px; */
  }
  .App{
    /* background-color: #1e2224; */
    /* background-color: #62bbe7; */
    background-color: #2093d5;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1vh;
  }
  .buttonContainer {
    background-color: #1b7db5;
    width: 20vw;
    height: 18vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20%;
  
    box-shadow: inset 5px 5px 10px #1b7db5,
              inset -5px -5px 10px #25a9f5;
  }
  .buttonContainer > button {
  font-size: 3.2vh;
  font-weight: 700;
  width: 16vw;
  height: 14vw;
  border: none;
  border-radius: 20%;
  background-color: #2093d5;
  box-shadow:  1px 1px 2px #0a1920,
               -1px -1px 2px #112431;
  /* box-shadow:  5px 5px 10px #43809e,
               -5px -5px 10px #5baed6; */
  /* box-shadow:  5px 5px 10px #3c738d,
               -5px -5px 10px #62bbe7; */
  
  }
  .buttonContainer button:active {
    background-color: rgb(83, 139, 185);
  }
  
  .smallButtons {
    /* background-color: #1b7db5; */
    width: 12vw;
    height: 12vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
            
    background-color: #464649;
    box-shadow: inset 7px 7px 14px #303032,
                inset -7px -7px 14px #5c5c60; 
     /* border-radius: 126px;
     background: #464649;
    box-shadow:  7px 7px 14px #303032,
                 -7px -7px 14px #5c5c60; */
  }
  .smallButtons button {
    font-size: 3.2vh;
    font-weight: 700;
    width: 10vw;
    height: 10vw;
    border: none;
    border-radius: 100%;
    color:white;
    /* background: #202122;
    box-shadow: inset 9px 9px 18px #1b1c1d,
              inset -9px -9px 18px #252627; */
  background-color: linear-gradient(137.81deg, #1D222E 21.35%, #293140 106.56%);
  background-color: rgb(87, 85, 85);
  mix-blend-mode: normal;
  border: 3px solid rgba(41, 49, 64, 0.65);
  box-sizing: border-box;
  box-shadow: -5px -5px 10px rgba(72, 80, 99, 0.1), 5px 5px 10px rgba
  (24, 28, 39, 0.35), inset -2px -2px 4px rgba(72, 80, 99, 0.35), inset 5px 5px 5px #1D222E
  }
  .topButtons {
    display: flex;
    width:  90vw;
    gap: 2vw;
    height: 10vh;
  }
  .text{
    margin-top: 1vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2vh;
    height: 27vh;
    /* background-color: blueviolet; */
  }
  
  .output, .input {
    resize: none;
    font-family:'Courier New', Courier, monospace;
    font-weight: 1000;
    border: none;
    border-radius: 2vh;
    width: 90vw;
    height: 8vh;
    /* background: #1A1A1A;
    box-shadow: inset 10px 10px 20px rgba(0, 0, 0, 0.25),
     inset -10px -10px 20px #202020; */
     background: #386aa3;
     background: #2093d5;
     box-shadow: inset 5px 5px 10px #2b517c,            inset -5px -5px 10px #4583ca;  
  }
  .input {
    font-size: 5vh;
    text-align: right;
    outline: none;
    color:rgb(19, 14, 17);
    display:flex;
    align-items: flex-end;
    justify-content:flex-end;
    height: 15vh;
    padding: 2vw;
  }
  .output {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 6vw;
  }
  .Buttons {
   display: grid;
   width: 90vw;
   grid-template-columns: auto auto auto auto;
   gap: 1vw;

   /* justify-content: center; */
   align-content: center;
  }
  .theme {
    /* background-color: red; */
    background-color: black;
    background-image: url("./images/ClockCounterClockwise.png");
    background-size: cover;
  }
  
  
